}
.univ_begin .input_area .form_wrap .search_msg .list li p {
  font-size: 13px;
}
.univ_begin .input_area .form_wrap .search_msg .list li button {
  font-size: 13px;
  border-radius: 5px;
  padding: 6px;
  color: #fff;
  background: #464646;
}
.univ_begin .input_area .form_wrap .btn_reset {
  display: block;
  padding: 3% 5%;
  margin: 0 auto;
  border-radius: 5px;
}
.univ_begin .input_area .form_wrap .btn_comp {
  display: block;
  width: 100%;
  padding: 5% 0%;
  margin-top: 3%;
  border-radius: 5px;
}
.univ_begin .input_area .form_wrap .notice_txt {
  margin-top: 3%;
  text-align: left;
  font-size: 12px;
  line-height: 14px;
  font-weight: normal;
