.animaition_bg {
  width: 100vw;
  height: 100vh;
  background: black;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 990;
}
.animaition_bg.off {
  display: none;
}

.main {
  position: relative;
  width: 100vw;
  height: calc(100vh - 72px);
  overflow: hidden;
}
.main .mySwiper {
  width: 100%;
  height: 100%;
}
.main .mySwiper .swiper-slide {
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}
.main .mySwiper .swiper-slide .bg_img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: -1;
  font-size: 200px;
  font-weight: bold;
  color: red;
  background: center no-repeat;
  background-size: cover;
}
.main .navigation {
  position: absolute;
  top: 90%;
  left: 0;
  z-index: 2;
  width: 100%;
}
.main .navigation .loading_bar {
  position: relative;
  margin: 0 auto;
  width: calc(100% - 64px);
  height: 1px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.2);
}
.main .navigation .loading_bar::after {
  content: "";
  position: absolute;
  top: 0;
  left: -13.5%;
  width: 13.5%;
  height: 100%;
  background: #fff;
}
@-webkit-keyframes loading_bar {
  0% {
    left: -13.5%;
  }
  100% {
    left: 100%;
  }
}
@-moz-keyframes loading_bar {
  0% {
    left: -13.5%;
  }
  100% {
    left: 100%;
  }
}
@keyframes loading_bar {
  0% {
    left: -13.5%;
  }
  100% {
    left: 100%;
  }
}
.main .navigation .loading_bar.on::after {
  animation: loading_bar 3.2s linear;
}
.main .navigation .arrow-btn {
  width: 60px;
  height: 40px;
  position: absolute;
  top: 2px;
  transition: all 0.3s;
  cursor: pointer;
}
.main .navigation .arrow-btn::after {
  display: none;
}
.main .navigation .arrow-btn i {
  position: absolute;
  top: 50%;
  width: 100%;
  height: 1px;
  background: #fff;
}
.main .navigation .arrow-btn i::after {
  content: "";
  position: absolute;
  top: 0;
  width: 50%;
  height: 1px;
  background: #fff;
}
.main .navigation .arrow-btn.swiper-button-next {
  right: 32px;
}
.main .navigation .arrow-btn.swiper-button-next i::after {
  right: 0;
  transform-origin: right;
  transform: rotate(35deg);
}
.main .navigation .arrow-btn.swiper-button-next:hover {
  right: 26px;
}
.main .navigation .arrow-btn.swiper-button-prev {
  left: 32px;
}
.main .navigation .arrow-btn.swiper-button-prev i::after {
  left: 0;
  transform-origin: left;
  transform: rotate(35deg);
}
.main .navigation .arrow-btn.swiper-button-prev:hover {
  left: 26px;
}